import React from "react";
import "./Logo.scss";

class Logo extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    this.updateCanvas();
  }
  updateCanvas() {
    const logoData = {
      lineWidth: 18,
      eyes: {
        centerPos: {
          x: 150,
          y: 75,
        },
        width: 18, // each eye
        height: 72,
        eyeSpacing: 18,
      },
      mouth: {
        centerPos: {
          x: 150,
          y: 205.5,
        },
        height: 107,
        width: 154,
        mouthShape: "default",
      },
    };
    const calculatePositions = function(data) {
      const eyes = logoData.eyes;
      const mouth = logoData.mouth;
      const eyesPos = {
        eyesTop: eyes.centerPos.y - (eyes.height / 2),
        eyesBottom: eyes.centerPos.y + (eyes.height / 2),
        eyeLeftX: eyes.centerPos.x - (eyes.eyeSpacing),
        eyeRightX: eyes.centerPos.x + (eyes.eyeSpacing),
      };
      logoData.eyesDrawData = {
        left: {
          top: {
            x: eyesPos.eyeLeftX,
            y: eyesPos.eyesTop,
          },
          bottom: {
            x: eyesPos.eyeLeftX,
            y: eyesPos.eyesBottom,
          },
        },
        right: {
          top: {
            x: eyesPos.eyeRightX,
            y: eyesPos.eyesTop,
          },
          bottom: {
            x: eyesPos.eyeRightX,
            y: eyesPos.eyesBottom,
          },
        }
      };
      logoData.mouthDrawData = {
        left: {
          x: mouth.centerPos.x - (mouth.width / 2),
          y: mouth.centerPos.y - (mouth.height / 2),
        },
        right: {
          x: mouth.centerPos.x + (mouth.width / 2),
          y: mouth.centerPos.y - (mouth.height / 2),
        },
        bottom: {
          x: mouth.centerPos.x,
          y: mouth.centerPos.y + (mouth.height / 2),
        },
      }
    }
    const draw = function() {
      const eyesDrawData = logoData.eyesDrawData;
      const mouthDrawData = logoData.mouthDrawData;
      ctx.clearRect(0, 0, 300, 300);
      ctx.beginPath();
  
      ctx.moveTo(eyesDrawData.left.top.x, eyesDrawData.left.top.y);
      ctx.lineTo(eyesDrawData.left.bottom.x, eyesDrawData.left.bottom.y);
  
      ctx.moveTo(eyesDrawData.right.top.x, eyesDrawData.right.top.y);
      ctx.lineTo(eyesDrawData.right.bottom.x, eyesDrawData.right.bottom.y);
  
      ctx.moveTo(mouthDrawData.left.x, mouthDrawData.left.y);
      ctx.lineTo(mouthDrawData.right.x, mouthDrawData.right.y);
  
      ctx.moveTo(mouthDrawData.left.x, mouthDrawData.left.y);
      ctx.bezierCurveTo(
        mouthDrawData.left.x, mouthDrawData.bottom.y,
        mouthDrawData.bottom.x, mouthDrawData.bottom.y,
        mouthDrawData.bottom.x, mouthDrawData.bottom.y
      );
  
      ctx.moveTo(mouthDrawData.right.x, mouthDrawData.right.y);
      ctx.bezierCurveTo(
        mouthDrawData.right.x, mouthDrawData.bottom.y,
        mouthDrawData.bottom.x, mouthDrawData.bottom.y,
        mouthDrawData.bottom.x, mouthDrawData.bottom.y
      );
  
      ctx.stroke();
    };

    const blink = function() {
      const eyesDrawData = logoData.eyesDrawData;
      const eyeTop = eyesDrawData.left.top.y;
      eyesDrawData.left.top.y = eyesDrawData.left.bottom.y;
      eyesDrawData.right.top.y = eyesDrawData.right.bottom.y;
      draw();
      const blink = setInterval(() => { 
        if (eyeTop < eyesDrawData.left.top.y) {
          eyesDrawData.left.top.y = (eyesDrawData.left.top.y - 10) > eyeTop
            ? (eyesDrawData.left.top.y - 10) : eyeTop;
          eyesDrawData.right.top.y = (eyesDrawData.right.top.y - 10) > eyeTop
            ? (eyesDrawData.right.top.y - 10) : eyeTop;
          draw();
        } else {
          clearInterval(blink);
        }
      }, 20)
    }
    const ctx = this.refs.canvas.getContext('2d');
    ctx.lineWidth = logoData.lineWidth;
    ctx.lineCap = "round";
    calculatePositions(logoData);

    draw();

    const temp = setInterval(()=>{
      blink();
    }, 2000);
  }
  render() {
    return (
      <div className="logo-wrapper">
        <canvas ref="canvas" id="my-canvas" width={this.props.width} height={this.props.height} />
      </div>
    );
  }
}

export default Logo;



